import { createContext, useContext, useEffect, useState } from "react";
import { Alert, Box, Card, CardContent, Container, Grid, ListItemButton, ListItemIcon, ListItemText, Typography } from '@mui/material';
import jsonData from '../data.json';
import caracData from '../carac.json';
import { useRef } from "react";
import ContentCopyIcon from '@mui/icons-material/ContentCopy';

export const MainContext = createContext({});

export default function MainProvider({ children }) {
    const codexList = jsonData;
    const caracJSON = caracData;
    const [selectedCodex, setSelectedCodex] = useState(null);
    const [selectedRare, setSelectedRare] = useState(null);
    const [selectedFaction, setSelectedFaction] = useState(null);
    const [formations, setFormations] = useState([]);
    const [selectedFormation, setSelectedFormation] = useState(null);
    const [cartes, setCartes] = useState([]);
    const [selectedCarteIndex, setSelectedCarteIndex] = useState(null);
    const [selectedFormationOptions, setSelectedFormationOptions] = useState(null);
    const [totalValue, setTotalValue] = useState(0);
    const [selectedCodexColor, setSelectedCodexColor] = useState('#969090'); // Couleur par défaut
    const [selectedCodexVersion, setSelectedCodexVersion] = useState(null);
    const [showCodexSelection, setShowCodexSelection] = useState(true);
    const [open, setOpen] = useState(false);
    const [openChange, setOpenChange] = useState(false);
    const [carteIndex, setCarteIndex] = useState([]);
    const [handleInputChange, sethandleInputChange] = useState([]);
    let file = null;
    const [refs, setRefs] = useState([]);
    const [filteredData, setfilteredData] = useState([]);
    const [openPrintDialog, setOpenPrintDialog] = useState(false);
    const [printList, setPrintList] = useState(true);
    const [printCarac, setPrintCarac] = useState(false);
    const [openDrawer, setOpenDrawer] = useState(false);
    const [infos, setInfos] = useState([]);
    const [formattedInfos, setFormattedInfos] = useState(null);
    const [allAccordionsExpanded, setAllAccordionsExpanded] = useState(true);
    const [accordionStates, setAccordionStates] = useState(cartes.map(() => true));
    const [optionAlerte, setOptionAlerte] = useState(null);
    const [alertRare, setAlertRare] = useState(false);
    const [totalRareValue, setTotalRareValue] = useState(0);

    const cartesSansNbacti = cartes.filter(carte => carte.options.some(option => option.nbacti !== false));
    // useEffect(()=>{  fonction qui doit etre executé a chanque variable ecouté()},[variable(s) ecouté])
    // useEffect(()=>{  setFormations()},[selectedCodex])

    //fonction pour fermer la page d'acceuil
    const handleCloseCodexSelection = () => {
        // Logique pour fermer la sélection de codex, si nécessaire
        setShowCodexSelection(false);
    };

    //fonction pour ouvrir la page d'acceuil
    const handleOpenCodexSelection = () => {
        setShowCodexSelection(true);
        setSelectedCodex(null);
        setSelectedFaction(null);
        setCartes([]); // Supprime toutes les cartes
        setSelectedCarteIndex(null); // Réinitialise la sélection
        setOpenChange(false);
        setSelectedFormation(null);
        setSelectedFormationOptions(null);
        setSelectedCodexColor('#9da9b3');
        handleToggleAllAccordions(true)

    };

    const handleClickOpenChange = () => {
        setOpenChange(true);
    };

    const handleCloseChange = () => {
        setOpenChange(false);
    };

    //fonction de la popup de départ
    function CodexSelectionPopup({ onClose, onSelectCodex }) {
        return (
            <Container >
                <Grid marginTop={1} spacing={2} container direction="row" justifyContent="center" alignItems="center">
                    {jsonData.map((codex) => (
                        <Grid item xs={12} md={3} key={codex.codex} className="popup-item" onClick={() => onSelectCodex(codex)}>
                            <Card>
                                <CardContent variant="outlined" style={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
                                    <img src={process.env.PUBLIC_URL + '/' + codex.logo} alt={codex.codex} />
                                    <Typography variant="body2" color="text.secondary"><b>{codex.codex}</b>  <i> {codex.version}</i> </Typography>
                                </CardContent>
                            </Card>
                        </Grid>
                    ))}
                </Grid>
            </Container>
        );
    }

    //fonction pour récupérer le codex de la pop up
    const handleSelectCodex = (codex) => {
        setShowCodexSelection(false);
        const selectedCodex = codex.codex;
        setSelectedCodex(selectedCodex);
        const selectedRare = codex.codexRare;
        setSelectedRare(selectedRare);
        const selectedFaction = codex.faction;
        setSelectedFaction(selectedFaction);
        // Filtrer les formations en fonction du codex sélectionné

        const selectedCodexData = codexList.find((codex) => codex.codex === selectedCodex);
        if (selectedCodexData) {
            console.log("selectedCodexData :", selectedCodexData.faction);
            setSelectedCodexVersion(selectedCodexData.version);
            setSelectedCodexColor(selectedCodexData.color || '#9da9b3'); // Utilisez la couleur du codex ou la couleur par défaut

            const allFormationsByGroups = selectedCodexData.groupes.map((groupe) => ({
                groupName: groupe.name,
                formations: groupe.formation,
                detail: groupe.detail,
            }));
            setFormations(allFormationsByGroups);
            setSelectedFormation(null); // Réinitialisez la sélection de la formation
        } else {
            setFormations([]);
            setSelectedFormation(null);
        }
        console.log("faction :", selectedFaction);
        console.log("color :", selectedCodexColor);
        console.log("version :", selectedCodexVersion);
    };

    //création d'une carte
    const handleCreateCarte = (formation) => {
        const newCarte = {
            formation: formation.name,
            options: [],
            optionbase: formation.options,
            reference: [], // Initialisation de la référence à un tableau vide
            rare: formation.rare,
            limiteOptionFormation: formation.limiteOptionFormation,
        };
        // Mettez à jour l'état accordionStates en ajoutant true pour la nouvelle carte
        setAccordionStates((prevStates) => [...prevStates, true]);

        // Ajouter la nouvelle carte à la liste
        const updatedCartes = [...cartes, newCarte];
        setCartes(updatedCartes);

        // Définir la nouvelle carte comme celle sélectionnée
        setSelectedCarteIndex(updatedCartes.length - 1);
    };

    //suppression carte
    const handleRemoveCarte = (index) => {
        const updatedCartes = [...cartes];
        updatedCartes.splice(index, 1);
        setCartes(updatedCartes);
    };

    // Fonction pour gérer la sélection d'une carte
    const handleCarteSelect = (carteIndex) => {
        setSelectedCarteIndex(carteIndex);
    };

    // Afficher les options disponibles lorsque la carte est sélectionnée
    const selectedCarte = selectedCarteIndex !== null ? cartes[selectedCarteIndex] : null;

    //la fonction chelou qui fait le lien entre selection et création
    const handleFormationAndCreateCarte = (formation) => {
        setSelectedFormation(formation);
        handleCreateCarte(formation);
        handleShowOptions(formation);
    };

    const handleShowOptions = (formation) => {
        const options = formation.options.map((option) => ({
            ...option,
            selectedValue: option.max ? 1 : 0, // 0 for options without max
        }));
        setSelectedFormationOptions(options);
    };

    //quand on clique sur le nom d'une formation on ne voit que le option - a modifier a terme
    const handleShowFormationOptions = (optionbase) => {
        setSelectedFormationOptions(optionbase);
    };

    const handleOptionSelect = (option, index, selectedValue) => {
        const updatedCartes = [...cartes];
        const selectedOption = { ...option, selectedValue };
        let alertMessage = null; // Variable pour stocker le message d'alerte
        alertMessage = null;

        // Contrôle 1: Vérifier au moins une option principale de formation
        const hasPrincipalOption = updatedCartes[index].options.some(opt => opt.principale !== undefined && opt.principale);
        console.log("principale", hasPrincipalOption)
        console.log("option.principale", option.principale)

        if ((hasPrincipalOption === false) && (option.principale === undefined || !option.principale)) {
            alertMessage = ("Sélectionne au moins une option de formation principale");
            setOptionAlerte(<Typography>{alertMessage}</Typography>)
            return;
        }

        // Contrôle 2: Vérifier l'unicité
        const hasSimilarOption = updatedCartes[index].options.some(opt => opt.unique === option.unique);
        console.log("hasSimilarOption", hasSimilarOption);
        console.log("option.unique", option.unique);
        if ((option.unique || option.unique !== undefined) && hasSimilarOption) {
            alertMessage = ("Tu as déjà sélectionné une option similaire");
            setOptionAlerte(<Typography>{alertMessage}</Typography>)
            return;
        }

        // Contrôle 3: Vérifier la limite d'option
        const limit = option.limit || 1;
        const selectedOptionCount = updatedCartes[index].options.filter(opt => opt.name === option.name).length;
        console.log("LimiteOption", selectedOptionCount)
        if (limit > 0 && selectedOptionCount >= limit) {
            // Message d'erreur
            alertMessage = ("Tu ne peux pas en ajouter plus");
            setOptionAlerte(<Typography>{alertMessage}</Typography>)
            return;
        }

        // Contrôle 4: Vérifier la limiteOptionGlobal
        if (option.limiteOptionGlobal) {
            const limitOptionGlobal = option.limiteOptionGlobal;
            const globalOptionCount = updatedCartes.reduce((total, carte) => total + carte.options.filter(opt => opt.name === option.name).length, 0);
            if (limitOptionGlobal > 0 && globalOptionCount >= limitOptionGlobal) {
                // Message d'erreur
                alertMessage = ("La limite pour cette option est atteinte dans la liste");
                setOptionAlerte(<Typography>{alertMessage}</Typography>)
                return;
            }
        }

        // Contrôle 5: Vérifier la limiteOptionFormation
        if (!option.principale && updatedCartes[index].limiteOptionFormation) {
            const limiteOptionFormation = updatedCartes[index].limiteOptionFormation || 0;
            const optionsSansPrincipaleCount = updatedCartes[index].options.filter(opt => !opt.principale).length;

            console.log("optionsSansPrincipaleCount", optionsSansPrincipaleCount);
            console.log("limiteOptionFormation", limiteOptionFormation);
            if ((optionsSansPrincipaleCount >= limiteOptionFormation)) {
                // Message d'erreur
                alertMessage = ("Limite d'options atteinte");
                setOptionAlerte(<Typography>{alertMessage}</Typography>)
                return;
            }
        }

        // Option ajoutée avec succès
        updatedCartes[index].options.push(selectedOption);
        setCartes(updatedCartes);
        setOptionAlerte(null);
        console.log("Carte", cartes);

    };

    //a chaque fois qu'une carte est mise a jour, on vérifie que les points d'iption rares n'est pas depassé
    useEffect(() => {
        // Calcule les points de formation rare à chaque changement de cartes
        const rareValue = calculateTotalRareValue(cartes);
        const totaleCarteValue = calculateTotalValue();
        // Met à jour l'état avec la nouvelle valeur
        setTotalRareValue(rareValue);
        if (rareValue > (selectedRare * totaleCarteValue)) {
            setAlertRare(true);
        }
        else {
            setAlertRare(false);
        }
    }, [cartes]);

    //vérifiation de la cohérence des formations rares
    const calculateTotalRareValue = (cartes) => {
        return cartes.reduce((total, carte) => {
            if (carte.rare) {
                return total + calculateCarteValue(carte);
            }
            return total;
        }, 0);
    };

    const AlerteAutoClose = ({ message }) => {

        useEffect(() => {
            const timer = setTimeout(() => {
                setOptionAlerte(null);
            }, 2500);

            return () => {
                clearTimeout(timer);
            };
        }, []);

        return (
            <div>
                {optionAlerte && (
                    <Alert severity="error">
                        {optionAlerte}
                    </Alert>
                )}
            </div>);
    };


    const handleOptionRemove = (option, carteIndex) => {

        const updatedCartes = [...cartes];
        const options = updatedCartes[carteIndex].options;
        const optionIndex = options.findIndex((opt) => opt.name === option.name);
        if (optionIndex !== -1) {
            options.splice(optionIndex, 1);
            setCartes(updatedCartes);
            // Mettez à jour le total ici
            setTotalValue(calculateTotalValue());
        }

    };

    const handleRemoveAllCartes = () => {
        setCartes([]); // Supprime toutes les cartes
        setSelectedCarteIndex(null); // Réinitialise la sélection
        setOpen(false);
    };

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    const calculateCarteValue = (carte) => {
        let sum = 0;
        if (carte.options) {
            carte.options.forEach((option) => {
                sum += option.value * option.selectedValue;
            });
        }
        return sum;
    };

    const calculateTotalValue = () => {
        let sum = 0;
        cartes.forEach((carte) => {
            sum += calculateCarteValue(carte);
        });
        return sum;
    };

    const maxCarte = cartes.reduce((prev, current) => {
        return calculateCarteValue(current) > calculateCarteValue(prev) ? current : prev;
    }, {});

    const handlePrint = () => {
        getAllSelectedRefs(); // Appeler la fonction pour obtenir les références sélectionnées
        // Attendre un court délai avant d'imprimer (pour permettre à getAllSelectedRefs de s'exécuter)
        setTimeout(() => {
            window.print(); // Appeler la fonction d'impression
        }, 500); // Ajuste le délai selon tes besoins (en millisecondes)
    };

    const exportAllCartes = () => {
        const codexName = selectedCodex || 'UnknownCodex';
        const codexFaction = selectedFaction || 'UnknownCodex';
        const points = totalValue || 0;
        const color = selectedCodexColor || '#969090';
        const version = selectedCodexVersion || 'UnknownVersion';


        const allCartes = {
            codexReference: codexName, // Ajouter une référence au codex
            codexFaction: codexFaction,
            points: points,
            cartes: cartes,
            color: color,
            version: version
        };
        // Convertir l'objet allCartes en chaîne JSON
        const jsonData = JSON.stringify(allCartes, null, 2);

        // Créer un objet Blob à partir des données JSON
        const blob = new Blob([jsonData], { type: 'application/json' });
        // Générer un URL pour télécharger le fichier JSON
        const url = URL.createObjectURL(blob);

        // Créer un élément d'ancre pour le téléchargement
        const link = document.createElement('a');
        link.href = url;
        link.download = `liste_${selectedCodex}_${calculateTotalValue()}pts.json`;

        // Simuler un clic sur le lien pour télécharger le fichier
        document.body.appendChild(link);
        link.click();

        // Nettoyer après le téléchargement
        document.body.removeChild(link);
        URL.revokeObjectURL(url);
    };

    const importCartesFromJSON = (file) => {
        const reader = new FileReader();
        reader.readAsText(file);
        reader.onload = (event) => {
            try {
                const importedData = JSON.parse(event.target.result);

                // Récupérer les données du fichier importé
                const importedCodexReference = importedData.codexReference; // Référence au codex
                const importedCartes = importedData.cartes; // Données des cartes
                const importedColor = importedData.color; // Couleur du codex
                const importedFaction = importedData.codexFaction; // Faction du codex

                // Mettre à jour selectedCodex avec la référence du fichier importé
                setSelectedCodex(importedCodexReference);
                setSelectedFaction(importedFaction);
                //cacher la popUp
                setShowCodexSelection(false);
                setSelectedFormationOptions(null);

                const selectedCodexData = codexList.find((codex) => codex.codex === importedCodexReference);
                if (selectedCodexData) {
                    setSelectedCodexVersion(selectedCodexData.version);
                    setSelectedCodexColor(selectedCodexData.color || '#9da9b3'); // Utilisez la couleur du codex ou la couleur par défaut
                    const allFormationsByGroups = selectedCodexData.groupes.map((groupe) => ({
                        groupName: groupe.name,
                        formations: groupe.formation,
                        detail: groupe.detail,
                    }));
                    setFormations(allFormationsByGroups);
                    setSelectedFormation(null);

                } else {
                    setFormations([]);
                    setSelectedFormation(null);
                }

                // ... le reste de votre logique pour mettre à jour les cartes
                setCartes(importedCartes);

            } catch (error) {
                console.error('Erreur lors de l\'importation du fichier :', error);
            }
        };
    };

    const handleFileChange = (event) => {
        setSelectedCodex('');
        setSelectedFaction('');
        setFormations([]);
        setCartes([]);
        setSelectedCarteIndex(null);
        if (file !== null) {
            file = null; // Réinitialiser le fichier s'il existe déjà
        }

        const newFile = event.target.files[0]; // Nouveau fichier à charger

        if (newFile) {
            file = newFile; // Affecter le nouveau fichier à la variable file
            importCartesFromJSON(file);
        }
        // Réinitialiser l'élément input de type file
        event.target.value = null;
    };

    // Fonction pour obtenir les références uniques des options sélectionnées
    const getAllSelectedRefs = () => {
        setRefs([]);
        const references = new Set(); // Utilisation de Set pour stocker des valeurs uniques

        cartes.forEach(carte => { // Parcours de chaque carte
            if (Array.isArray(carte.ref)) { // Vérifie si la référence est un tableau
                carte.ref.forEach(subRef => { // Parcours chaque élément du tableau
                    if (subRef !== undefined) { // Ajoute l'élément au Set 'references' si ce n'est pas undefined
                        references.add(subRef);
                    }
                });
            } else if (carte.ref !== undefined) { // Ajoute la référence au Set 'references' si ce n'est pas undefined
                references.add(carte.ref);
            }

            carte.options.forEach(option => { // Parcours chaque option dans la carte
                if (option.selectedValue > 0) { // Vérifie si l'option est sélectionnée
                    if (Array.isArray(option.ref)) { // Vérifie si la référence est un tableau
                        option.ref.forEach(subRef => { // Parcours chaque élément du tableau
                            if (subRef !== undefined) { // Ajoute l'élément au Set 'references' si ce n'est pas undefined
                                references.add(subRef);
                            }
                        });
                    } else if (option.ref !== undefined) { // Ajoute la référence au Set 'references' si ce n'est pas undefined
                        references.add(option.ref);
                    }
                }
            });
        });

        const referencesArray = [...references]; // Convertit le Set en tableau
        const filteredReferences = referencesArray.filter(ref => ref !== undefined); // Filtre les undefined du tableau

        setRefs(filteredReferences); // Met à jour l'état ou la variable 'refs' sans les undefined
        return filteredReferences; // Renvoie le tableau filtré
    };

    useEffect(() => { filterData() }, [refs]); // mets a jour filtereddata a chaque fois que refs change

    // focntion pour mettre en forme les options présente dans la liste
    const filterData = () => {
        if (selectedFaction && refs) {
            const filteredByCodex = caracJSON.filter(item => item.faction === selectedFaction);
            const filtered = [];
            filteredByCodex.forEach(item => {
                item.stat.forEach(stat => {
                    if (refs.includes(stat.Nom)) {
                        filtered.push({
                            Nom: stat.Nom,
                            Type: stat.Type,
                            Rang: stat.rang,
                            Vitesse: stat.Vitesse,
                            Blindage: stat.Blindage,
                            CC: stat.CC,
                            FF: stat.FF,
                            Notes: stat.Notes,
                            armes: stat.armes.map(arme => `${arme.Arme} - ${arme.Portée} - ${arme['Puissance de feu']}`),
                        });
                    }
                });
            });
            filtered.sort((a, b) => a.Rang - b.Rang);
            setfilteredData(filtered);
        }
    };

    //Fonction pour recupérer les caract de l'option selectionné
    function getInfoByOption(option) {
        if (option && option.ref && caracJSON) {
            const infos = [];
            // Filtrer caracJSON par faction
            const filteredCaracJSON = caracJSON.filter(item => item.faction === selectedFaction);
            console.log("filteredCaracJSON", filteredCaracJSON)
            const matchingStats = filteredCaracJSON.filter(item => {
                const matchingRefs = item.stat.filter(stat => {
                    // Si la propriété ref est un tableau, on vérifie si l'option.ref est inclus
                    if (Array.isArray(option.ref)) {
                        return option.ref.includes(stat.Nom);
                    } else {
                        return stat.Nom === option.ref;
                    }
                });
                return matchingRefs.length > 0;
            });
            matchingStats.forEach(matchingStat => {
                matchingStat.stat.forEach(stat => {
                    // Si la propriété ref est un tableau, on vérifie si l'option.ref est inclus
                    if (Array.isArray(option.ref) && option.ref.includes(stat.Nom)) {
                        const info = {
                            Nom: stat.Nom,
                            Type: stat.Type,
                            Rang: stat.rang,
                            Vitesse: stat.Vitesse,
                            Blindage: stat.Blindage,
                            CC: stat.CC,
                            FF: stat.FF,
                            Notes: stat.Notes,
                            armes: stat.armes.map(arme => `${arme.Arme} - ${arme.Portée} - ${arme['Puissance de feu']}`),
                        };
                        infos.push(info);
                    } else if (!Array.isArray(option.ref) && stat.Nom === option.ref) {
                        // Si la propriété ref est une chaîne simple, on vérifie l'égalité
                        const info = {
                            Nom: stat.Nom,
                            Type: stat.Type,
                            Rang: stat.rang,
                            Vitesse: stat.Vitesse,
                            Blindage: stat.Blindage,
                            CC: stat.CC,
                            FF: stat.FF,
                            Notes: stat.Notes,
                            armes: stat.armes.map(arme => `${arme.Arme} - ${arme.Portée} - ${arme['Puissance de feu']}`),
                        };
                        infos.push(info);
                    }
                });
            });

            return infos;
        } else {
            return [];
        }
    };
    //Fonction pour Mettre en forme les caract de l'option selectionné
    function formattedContent(infos) {
        return (
            <Box >
                <Grid container spacing={2} marginBottom={2} width={'50vw'}>
                    <Grid item xs={2}>
                        <Typography variant='caption'>Nom</Typography>
                    </Grid>
                    <Grid item xs={1}>
                        <Typography variant='caption'>Type</Typography>
                    </Grid>
                    <Grid item xs={0.5}>
                        <Typography variant='caption'>Vit</Typography>
                    </Grid>
                    <Grid item xs={0.5}>
                        <Typography variant='caption'>Bl</Typography>
                    </Grid>
                    <Grid item xs={0.5}>
                        <Typography variant='caption'>CC</Typography>
                    </Grid>
                    <Grid item xs={0.5}>
                        <Typography variant='caption'>FF</Typography>
                    </Grid>
                    <Grid item xs={4}>
                        <Typography variant='caption'>Armes / Portée / Puissance</Typography>
                    </Grid>
                    <Grid item xs={3}>
                        <Typography variant='caption'>Notes</Typography>
                    </Grid>
                </Grid>

                {infos.map((row, index) => (
                    <Grid container width={'50vw'} item key={index} alignItems={'center'} style={{ borderTop: 1, backgroundColor: index % 2 === 0 ? '#838383' : '3F3F3F', }}                       >
                        <Grid item xs={2}>
                            <Typography variant='caption'>{row.Nom}</Typography>
                        </Grid>
                        <Grid item xs={1}>
                            <Typography variant='caption'>{row.Type}</Typography>
                        </Grid>
                        <Grid item xs={0.5}>
                            <Typography variant='caption'>{row.Vitesse}</Typography>
                        </Grid>
                        <Grid item xs={0.5}>
                            <Typography variant='caption'>{row.Blindage}</Typography>
                        </Grid>
                        <Grid item xs={0.5}>
                            <Typography variant='caption'>{row.CC}</Typography>
                        </Grid>
                        <Grid item xs={0.5}>
                            <Typography variant='caption'>{row.FF}</Typography>
                        </Grid>
                        <Grid item xs={4}>
                            <Grid Container alignContent={'center'}>
                                {row.armes.map((arme, idx) => (
                                    <Grid item xs={12} key={idx}>
                                        <Typography key={idx} variant='caption' sx={{ fontSize: '7pt' }}>
                                            {arme}
                                        </Typography>
                                    </Grid>
                                ))}
                            </Grid>
                        </Grid>
                        <Grid item xs={3}>
                            <Typography variant='caption' sx={{ fontSize: '8pt' }}>
                                {row.Notes}
                            </Typography>
                        </Grid>
                    </Grid>
                ))}

            </Box >
        );
    };

    // Fonction pour mettre à jour les informations en fonction de l'option sélectionnée
    const updateInfos = (option) => {
        // Obtenez les informations brutes
        const rawInfos = getInfoByOption(option);
        // Mettez à jour l'état des informations brutes
        setInfos(rawInfos);
        // Formatez les informations dans la boîte
        const content = formattedContent(rawInfos);
        // Mettez à jour l'état avec le contenu formaté
        setFormattedInfos(content);
    };

    const displayPrintValue = printList ? 'Block' : 'none';
    const displayCaracValue = printCarac ? 'Block' : 'none';

    const handleOpenPrintDialog = () => {
        setOpenPrintDialog(true);
    };

    const handleClosePrintDialog = () => {
        setOpenPrintDialog(false);
    };

    const handlePrintListChange = (event) => {
        setPrintList(event.target.checked);
    };

    const handlePrintCaracChange = (event) => {
        setPrintCarac(event.target.checked);
    };


    const handleDrawerOpen = () => {
        setOpenDrawer(true);
    };

    const handleDrawerClose = () => {
        setOpenDrawer(false);
    };

    // Fonction de contrôle des références pour les caracts
    const checkReferences = () => {
        const missingReferences = [];

        // Parcourez chaque faction dans data.json
        codexList.forEach((codex) => {
            const faction = codex.faction;

            // Obtenez la liste des références dans data.json pour cette faction
            const dataReferences = codex.groupes.flatMap((groupe) =>
                groupe.formation.flatMap((formation) =>
                    formation.options.flatMap((option) => (option.ref ? option.ref : []))
                )
            );

            // Obtenez la liste des noms dans carac.json pour cette faction
            const caracNames = caracJSON
                .filter((item) => item.faction === faction)
                .flatMap((item) => item.stat.map((stat) => stat.Nom));

            // Recherchez les références manquantes
            dataReferences.forEach((ref) => {
                if (!caracNames.includes(ref)) {
                    missingReferences.push({ faction, ref });
                }
            });
        });

        // Affichez les références manquantes dans la console
        console.log('Références manquantes :', missingReferences);
    };


    // Appel de la fonction lors du chargement de la page
    window.onload = checkReferences;

    const CopyText = () => {
        const { selectedCodex, calculateTotalValue, cartes, maxCarte } = useContext(MainContext);
        const [isCopied, setIsCopied] = useState(false);

        const handleCopyToClipboard = () => {
            const contentToCopy =
                `## Liste ${selectedCodex} ${calculateTotalValue()} pts \r Nombre de formations ***${cartes.length}*** \r ***BLM : ${maxCarte.formation} ${calculateCarteValue(maxCarte)} pts***
            ${cartes.map((carte, carteIndex) => (
                    `
                - ***${carte.formation} - ${calculateCarteValue(carte)}*** 
                ${carte.options.map((option, optionIndex) => {
                        const quantityString = option.selectedValue > 1 ? `${option.selectedValue} ` : '';
                        return ` - ${quantityString}${option.name} - ${option.value * option.selectedValue}\r`;
                    }).join('')}
              `
                )).join('')
                }
        `;

            navigator.clipboard.writeText(contentToCopy)
                .then(() => {
                    setIsCopied(true);
                    setTimeout(() => {
                        setIsCopied(false);
                    }, 2000);
                })
                .catch((error) => {
                    console.error('Erreur lors de la copie dans le presse-papiers :', error);
                });
        };

        return (
            <ListItemButton>
                <ListItemIcon>
                    <ContentCopyIcon onClick={handleCopyToClipboard} tooltipTitle='Copier pour Discord' />
                    {isCopied ? 'Copié !' : ''}
                </ListItemIcon>
                <ListItemText primary="Copier pour Discord" onClick={handleCopyToClipboard} tooltipTitle='Copie Discord' />
            </ListItemButton>
        );
    };

    //Etat pour tout les accordeon
    const handleToggleAllAccordions = (value) => {
        setAccordionStates(Array(cartes.length).fill(value));
    };


    //pour controler l'etat de chaque accordeons
    const handleToggleAccordion = (index) => {
        setAccordionStates((prevStates) => {
            const newStates = [...prevStates];
            newStates[index] = !newStates[index];
            return newStates;
        });
    };

    return (
        <MainContext.Provider value={
            {
                cartesSansNbacti,
                totalRareValue,
                optionAlerte,
                alertRare,
                AlerteAutoClose,
                handleToggleAccordion,
                setAccordionStates,
                accordionStates,
                allAccordionsExpanded,
                handleToggleAllAccordions,
                formattedInfos,
                infos,
                updateInfos,
                CopyText,
                handleDrawerOpen, openDrawer,
                handleDrawerClose,
                displayCaracValue,
                displayPrintValue,
                handleOpenPrintDialog,
                handleClosePrintDialog,
                openPrintDialog,
                refs,
                filterData,
                filteredData,
                getAllSelectedRefs,
                exportAllCartes,
                handleInputChange,
                handleFileChange,
                selectedCodexColor,
                selectedCarte,
                selectedCodex,
                selectedCodexVersion,
                selectedFormation,
                selectedFormationOptions,
                selectedCarteIndex,
                maxCarte,
                handleOptionRemove,
                handleRemoveCarte,
                handleShowFormationOptions,
                handleCarteSelect,
                handleSelectCodex,
                handleRemoveAllCartes,
                handleOptionSelect,
                handleFormationAndCreateCarte,
                handleShowOptions,
                handleOpenCodexSelection,
                formations,
                showCodexSelection,
                CodexSelectionPopup,
                handleCloseCodexSelection,
                codexList,
                cartes,
                calculateTotalValue,
                calculateCarteValue,
                jsonData,
                handlePrint,
                open,
                handleClickOpen,
                handleClose,
                openChange,
                handleClickOpenChange,
                handleCloseChange,
                carteIndex,
                printCarac,
                printList,
                handlePrintCaracChange,
                handlePrintListChange,

            }}>
            {children}
        </MainContext.Provider>
    )
}